.selectWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
}

.wrapper {
  display: flex;
  justify-content: end;
  align-items: center;
  width: 100%;
  margin: 10px 0px;
}

.inputLabel {
  color: var(--primary-black);
  text-align: right;
  margin-right: 10px;

  @media(max-width: 400px) {
    min-width: 86px;
  }
}

.input {
  border: none;
  border-bottom: 1px solid var(--gray-dark);
  background: transparent;
  padding: 5px 10px;
  width: 100%;
  outline: none;
  font-size: 14px;

  &::placeholder {
    font-size: 14px;
  }

  &::-webkit-search-cancel-button {
    -webkit-appearance: none;
    width: 8px;
    height: 8px;
    background: url('../../assets/icons/CloseIcon.svg');
    margin-right: 6px;
    margin-bottom: 2px;
    cursor: pointer;
  }
}

.option {
  width: 100%;
  z-index: 999;
  background: white;
  border: none;
  padding: 12px;
  font-size: 14px;
  text-align: start;
  cursor: pointer;
  transition: all 0.3s ease;

  &Active {
    color: var(--primary-green);
  }

  &:hover {
    color: var(--primary-green);
  }
}

.optionWrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  min-width: 250px;
  max-height: 200px;
  position: absolute;
  font-size: 14px;
  top: 30px;
  z-index: 888;
  overflow: auto;
  background: #fff;
  border: 1px solid var(--gray-dark);
  border-radius: 2px;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s ease;

  &::-webkit-scrollbar {
    border-left: 1px solid var(--gray-dark);
    max-width: 5px;
  }

  &::-webkit-scrollbar-thumb {
    background: var(--gray-dark);
    border: 0.1px solid var(--gray-dark);
    border-radius: 999px;
  }

  &Open {
    opacity: 1;
    pointer-events: all;
  }
}

.notFound {
  padding: 15px;
  font-size: 14px;
  font-weight: 300;
  text-align: center;
}
